.pageContainer {
    overflow: auto;
    height: 92.5vh;
    width: 100vw;
    max-width: 100vw;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: row;
    }

.subDivOne {
    background-image: url('../images/side.png');
    background-size: 100% 100%;
    opacity: .75;
    width: 35%;
    min-height: 100%;
    max-height: 100%;
}

.subDivTwo {
    height: 100%;
    width: 30%;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
}

.subDivThree {
    background-image: url('../images/sideinv.png');
    background-size: 100% 100%;
    opacity: .75;
    width: 35%;
    min-height: 100%;
    max-height: 100%;
}
